.radio-body{
font-family: 'Roboto';
font-style: normal;
font-weight: 500;
letter-spacing: 0.04em;
font-size: 12px;
display: flex;
flex-direction: row;
justify-content: space-between;
align-items: center;
padding: 16px 22px;
gap: 10px;
background: rgba(117, 0, 0, 0.02);
border-radius: 19px;
transition: background 1.3s;
transition: opacity 0.3s;
flex: none;
order: 0;
align-self: stretch;
flex-grow: 0;
}

.radio-body.true{
	cursor: default;
	opacity: 0.5;
}
.radio-body.false{
	cursor: pointer;
	color: #000000;
	
}
 
 
input[type="radio"] {
	cursor: pointer;
	appearance: none;
	border: 2px solid #000000; /* Change color code here */
	border-radius: 50%;
	width: 12px;
	height: 12px;
	outline: none;
	transition: background 1.3s;
	transition: opacity 0.3s;

  }
  
  input[type="radio"]:checked {
	border-color: #000000; /* Change color code here */
	background: #000000;
  }
  
  input[type="radio"]:disabled {
	opacity: 0.5;
	cursor: default ;
  }